import React, { Component } from 'react';
import { Row, Col, Form, Input, Select } from 'antd';
import { findIndex } from 'lodash';
import { BtnSearch, BtnClear } from 'components/common/button';
import './FormSearchStaff.scss';

const { Option } = Select;

@Form.create({ name: 'FormSearchFormerStaff' })
class FormSearchStaffProfile extends Component {
  state = {};

  // componentDidMount = () => {
  //   const { form } = this.props;
  // };

  isValidYears = years => findIndex(years, year => !/^(19[5-9]\d|20[0-4]\d|2050)$/.test(year)) >= 0

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log(values)
      const { years } = values;
      if(years) {
        if(this.isValidYears(years)) {
          this.props.form.setFields({
            years: {
              value: years,
              errors: [new Error('Năm không hợp lệ')],
            },
          });
        } else {
          this.props.form.setFields({
            years: {
              value: years,
            },
          });
          this.props.onSearch(values)
        }
      }
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  // handleChange = value => {
  //   console.log(this.props.form.getFieldValue('years'))
  //   if(!/^(19[5-9]\d|20[0-4]\d|2050)$/.test(value)) {
  //     this.props.form.setFields({
  //       years: {
  //         value: ['2019'],
  //         errors: [new Error('Năm không hợp lệ')],
  //       },
  //     });
  //   }
  // }

  render() {
    const { form: { getFieldDecorator } } = this.props;
    return (
      <Form className="form-inline-label search-form staff-former-search-form" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Họ và tên">
              {getFieldDecorator('fullname', {})(
                <Input />
              )}  
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Mã">
              {getFieldDecorator('staffId', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Giới tính">
              {getFieldDecorator('gender', {})(
                <Select>
                  <Option value="" />
                  <Option value>Nam</Option>
                  <Option value={false}>Nữ</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm">
              {
                getFieldDecorator('years', {})
                  (
                    <Select
                      mode="tags"
                      defaultValue={undefined}
                      onChange={this.handleChange}
                    />
                  )
              }
            </Form.Item>
          </Col>
          <Col span={4}>  
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStaffProfile;



